

















































































































import 'reflect-metadata'
import { Component, Vue } from 'vue-property-decorator'
import Dropdown from '@/components/misc/Dropdown.vue'
import AvaxInput from '@/components/misc/AvaxInput.vue'
import Big from 'big.js'
import AvaAsset from '@/js/AvaAsset'
import { BN } from 'avalanche'
import { avm, pChain } from '@/AVA'
import AvaHdWallet from '@/js/wallets/AvaHdWallet'
import { bnToBig } from '@/helpers/helper'
import Spinner from '@/components/misc/Spinner.vue'
import ChainCard from '@/components/wallet/earn/ChainTransfer/ChainCard.vue'
import TxStateCard from '@/components/wallet/earn/ChainTransfer/TxState.vue'
import { ChainSwapFormData, TxState } from '@/components/wallet/earn/ChainTransfer/types'
import { ChainIdType } from '@/constants'

import ChainSwapForm from '@/components/wallet/earn/ChainTransfer/Form.vue'

import { web3 } from '@/evm'

const IMPORT_DELAY = 4000 // in ms

@Component({
    name: 'chain_transfer',
    components: {
        Spinner,
        Dropdown,
        AvaxInput,
        ChainCard,
        ChainSwapForm,
        TxStateCard,
    },
})
export default class ChainTransfer extends Vue {
    $refs!: {
        form: ChainSwapForm
    }
    sourceChain: ChainIdType = 'X'
    targetChain: ChainIdType = 'P'
    isLoading = false
    amt: BN = new BN(0)
    err: string = ''

    isImportErr = false
    isConfirm = false
    isSuccess = false

    formAmt = new BN(0)

    // Transaction ids
    exportId: string = ''
    exportState: TxState = TxState.waiting
    exportStatus: string | null = null
    exportReason: string | null = null

    importId: string = ''
    importState: TxState = TxState.waiting
    importStatus: string | null = null
    importReason: string | null = null

    // switchChain() {
    //     let temp = this.sourceChain
    //     this.sourceChain = this.targetChain
    //     this.targetChain = temp
    // }
    activated() {
        this.$refs.form.clear()
    }

    get ava_asset(): AvaAsset | null {
        let ava = this.$store.getters['Assets/AssetAVA']
        return ava
    }

    get platformUnlocked(): BN {
        return this.$store.getters['Assets/walletPlatformBalance']
    }

    get platformLocked(): BN {
        return this.$store.getters['Assets/walletPlatformBalanceLocked']
    }

    get avmUnlocked(): BN {
        if (!this.ava_asset) return new BN(0)
        return this.ava_asset.amount
    }

    get evmUnlocked(): BN {
        let balRaw = this.wallet.ethBalance
        return balRaw.divRound(new BN(Math.pow(10, 9)))
    }

    get balance(): Big {
        let bal: BN
        if (this.sourceChain === 'P') {
            bal = this.platformUnlocked
        } else if (this.sourceChain === 'C') {
            bal = this.evmUnlocked
        } else {
            bal = this.avmUnlocked
        }

        let bigBal = bnToBig(bal, 9)

        return bigBal
    }

    get formAmtText() {
        return bnToBig(this.formAmt, 9).toLocaleString()
    }

    // Fee is 2 times the tx transfer fee
    get fee(): Big {
        let feeX = avm.getTxFee()
        let totFee = feeX.mul(new BN(2))

        if (this.targetChain === 'C') {
            totFee = feeX
        }

        let feeXBig = bnToBig(totFee, 9)

        return feeXBig
    }

    get maxAmt(): BN {
        let max = this.balance.sub(this.fee)
        let zero = Big(0)
        if (zero.gt(max)) {
            return new BN(0)
        } else {
            let bnStr = max.times(Big(Math.pow(10, 9))).toString()
            return new BN(bnStr)
        }
    }

    get amtTotalCost(): Big {
        let amt = this.amtBig
        let fee = this.fee
        return amt.add(fee)
    }

    get amtBig(): Big {
        let bn = this.amt
        // let big = Big(bn.toString()).div(Math.pow(10,9));
        let big = bnToBig(bn, 9)
        return big
    }

    onFormChange(data: ChainSwapFormData) {
        this.amt = data.amount
        this.sourceChain = data.sourceChain
        this.targetChain = data.destinationChain
    }

    confirm() {
        this.formAmt = this.amt.clone()
        this.isConfirm = true
    }

    cancelConfirm() {
        this.isConfirm = false
        this.formAmt = new BN(0)
    }

    get wallet() {
        let wallet: AvaHdWallet = this.$store.state.activeWallet
        return wallet
    }

    async submit() {
        this.err = ''
        this.isLoading = true
        this.isImportErr = false

        try {
            this.chainExport(this.formAmt, this.sourceChain, this.targetChain).catch((e) => {
                this.onerror(e)
            })
        } catch (err) {
            this.onerror(err)
        }
    }

    // Triggers export from chain
    // STEP 1
    async chainExport(amt: BN, sourceChain: ChainIdType, destinationChain: ChainIdType) {
        let wallet: AvaHdWallet = this.$store.state.activeWallet
        let exportTxId
        this.exportState = TxState.started

        let nonce = 0
        if (this.sourceChain === 'C') {
            nonce = await web3.eth.getTransactionCount(this.wallet.ethAddress)
        }

        try {
            exportTxId = await wallet.chainTransfer(amt, sourceChain, destinationChain)
        } catch (e) {
            throw e
        }

        this.exportId = exportTxId
        this.waitExportStatus(exportTxId, nonce)
    }

    // STEP 2
    async waitExportStatus(txId: string, nonce?: number, remainingTries = 15) {
        let status
        if (this.sourceChain === 'X') {
            status = await avm.getTxStatus(txId)
        } else if (this.sourceChain === 'P') {
            let resp = await pChain.getTxStatus(txId)
            if (typeof resp === 'string') {
                status = resp
            } else {
                status = resp.status
                this.exportReason = resp.reason
            }
        } else {
            // We can check the nonce to see when the tx is confirmed
            let nonceNow = await web3.eth.getTransactionCount(this.wallet.ethAddress)
            if (nonceNow === nonce) {
                status = 'Processing'
            } else {
                status = 'Accepted'
            }
        }
        this.exportStatus = status

        if (status === 'Unknown' || status === 'Processing') {
            // If out of tries
            if (remainingTries <= 0) {
                this.exportState = TxState.failed
                this.exportStatus = 'Timeout'
                return false
            }

            // if not confirmed ask again
            setTimeout(() => {
                this.waitExportStatus(txId, nonce, remainingTries - 1)
            }, 1000)
            return false
        } else if (status === 'Dropped') {
            // If dropped stop the process
            this.exportState = TxState.failed
            return false
        } else {
            // If success start import
            this.exportState = TxState.success

            // Because the API nodes are behind a load balancer we are waiting for all api nodes to update
            this.importState = TxState.started
            this.importStatus = 'Waiting'
            setTimeout(() => {
                this.chainImport()
            }, IMPORT_DELAY)
        }

        return true
    }

    // STEP 3
    async chainImport() {
        let wallet: AvaHdWallet = this.$store.state.activeWallet
        let importTxId
        try {
            if (this.targetChain === 'P') {
                importTxId = await wallet.importToPlatformChain()
            } else if (this.targetChain === 'X') {
                importTxId = await wallet.importToXChain(this.sourceChain)
            } else {
                importTxId = await wallet.importToCChain()
            }
        } catch (e) {
            this.onerror(e)
            this.onErrorImport(e)
            return
        }

        this.importId = importTxId
        this.importState = TxState.started

        this.waitImportStatus(importTxId)
    }

    // STEP 4
    async waitImportStatus(txId: string) {
        let status

        if (this.targetChain === 'X') {
            status = await avm.getTxStatus(txId)
        } else if (this.targetChain === 'P') {
            let resp = await pChain.getTxStatus(txId)
            if (typeof resp === 'string') {
                status = resp
            } else {
                status = resp.status
            }
        } else {
            // TODO: Add evm processing
            status = 'Accepted'
        }

        this.importStatus = status

        if (status === 'Unknown' || status === 'Processing') {
            // if not confirmed ask again
            setTimeout(() => {
                this.waitImportStatus(txId)
            }, 1000)
            return false
        } else if (status === 'Dropped') {
            // If dropped stop the process
            this.importState = TxState.failed
            return false
        } else {
            // If success display success page
            this.importState = TxState.success
            this.onsuccess()
        }

        return true
    }

    onerror(err: any) {
        console.error(err)
        this.isLoading = false
        this.err = err
        this.$store.dispatch('Notifications/add', {
            type: 'error',
            title: 'Transfer Failed',
            message: err,
        })
    }

    onErrorImport(err: any) {
        this.isImportErr = true
    }

    startAgain() {
        this.$refs.form.clear()

        this.err = ''
        this.isImportErr = false
        this.isConfirm = false
        this.isLoading = false
        this.isSuccess = false

        this.exportId = ''
        this.exportState = TxState.waiting
        this.exportStatus = null
        this.exportReason = null

        this.importId = ''
        this.importState = TxState.waiting
        this.importStatus = null
        this.importReason = null
    }

    onsuccess() {
        // Clear Form
        this.isSuccess = true
        this.$store.dispatch('Notifications/add', {
            type: 'success',
            title: 'Transfer Complete',
            message: 'Funds transferred between chains.',
        })

        if (this.targetChain === 'C') {
            setTimeout(() => {
                this.$store.dispatch('Assets/updateUTXOs')
                this.$store.dispatch('History/updateTransactionHistory')
            }, 3000)
        } else {
            this.$store.dispatch('Assets/updateUTXOs')
            this.$store.dispatch('History/updateTransactionHistory')
        }
    }

    get canSubmit() {
        if (this.amt.eq(new BN(0))) {
            return false
        }

        if (this.amt.gt(this.maxAmt)) {
            return false
        }

        return true
    }
}
