






























import 'reflect-metadata'
import { Vue, Component, Prop } from 'vue-property-decorator'

import Spinner from '@/components/misc/Spinner.vue'
import { WalletType } from '@/store/types'
@Component({
    components: { Spinner },
})
export default class ChainImport extends Vue {
    err = ''
    isSuccess = false
    isLoading = false
    txId = ''

    get wallet(): null | WalletType {
        let wallet: null | WalletType = this.$store.state.activeWallet
        return wallet
    }

    // TODO: Remove after ledger support
    get isLedger() {
        if (!this.wallet) return false
        if (this.wallet.type === 'ledger') return true
        return false
    }
    async atomicImportX() {
        this.beforeSubmit()
        if (!this.wallet) return

        // Import from P
        try {
            let txId = await this.wallet.importToXChain('P')
            this.onSuccess(txId)
        } catch (e) {
            if (this.isSuccess) return
            this.onError(e)
        }

        // Import from C
        try {
            // TODO: Change after ledger support
            if (this.wallet.type !== 'ledger') {
                let txId2 = await this.wallet.importToXChain('C')
                this.onSuccess(txId2)
            }
        } catch (e) {
            if (this.isSuccess) return
            this.onError(e)
        }
    }

    async atomicImportP() {
        this.beforeSubmit()
        if (!this.wallet) return
        try {
            let txId = await this.wallet.importToPlatformChain()
            this.onSuccess(txId)
        } catch (e) {
            this.onError(e)
        }
    }

    async atomicImportC() {
        this.beforeSubmit()
        if (!this.wallet) return
        try {
            let txId = await this.wallet.importToCChain()
            this.onSuccess(txId)
        } catch (e) {
            this.onError(e)
        }
    }

    deactivated() {
        this.err = ''
        this.txId = ''
        this.isSuccess = false
    }

    beforeSubmit() {
        this.isLoading = true
        this.err = ''
        this.isSuccess = false
        this.txId = ''
    }

    onSuccess(txId: string) {
        this.isLoading = false
        this.err = ''
        this.isSuccess = true
        this.txId = txId

        this.$store.dispatch('Notifications/add', {
            type: 'success',
            title: 'Import Success',
            message: txId,
        })

        setTimeout(() => {
            this.$store.dispatch('Assets/updateUTXOs')
            this.$store.dispatch('History/updateTransactionHistory')
        }, 3000)
    }

    onError(err: Error) {
        this.isLoading = false
        console.error(err)
        let msg = ''
        if (err.message.includes('No atomic')) {
            this.err = 'Nothing found to import.'
            return
        } else {
            this.err = err.message
        }
    }
}
